body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Montserrat', sans-serif;
}
#content_bin {
    padding-top: 15px;
    max-width: 1300px;
    margin: 0 auto;
}
#root {
    height: 100vh;
    overflow-y: scroll;
}

#mob-stats .field,
#mob-stats .field input,
#mob-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label {
    text-align: left;
}

#areas-list .list,
#mobs-list .list {
    text-align: left;
}

#object-stats .field,
#object-stats .field input,
#object-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label button.ui.icon.button {
    padding: 0;
    display: inline;
    background: transparent;
    color: rgba(0,0,0,.87);
}
#dashboard p {
    margin: 0 auto;
    max-width: 900px;
    text-align: center;
}

#mobs-list .list .item .ui.text.menu a {
    padding: 0;
}

#mobs-list .list .item .ui.text.menu a:after {
    content: "|";
    padding: 5px;
}

#mobs-list .list .item .ui.text.menu a:last-of-type:after {
    content: "";
    padding: 0;
}

#mobs-list .list .item .ui.text.menu {
    margin: 0;
    padding: 0;
}


.ui.segment.scale.visible.transition {
    text-align: center;
}

.ui.container {
	padding: 2em;
	border: 1px solid #ececec;
	background-color: #fff;
	margin: 2em 0;
}

.ui.container .header {
	overflow: visible;
}

.ui.container .header button {
    position: absolute;
    top: -7px;
    color: #fff;
    min-width: 150px;
    background-color: #ff5900;
    text-shadow: 1px 1px 1px #282c34;
}

.container .segments .segment.exit {
    padding-top: 75px;
}

.container .segments .segment.exit .rail .segment {
    background-color: #ececec;
    color: #000;
    font-weight: bold;
    text-transform: capitalize;
}

/**
 * AREAS STYLING
 */
#areas-list .ui.card,
#areas-list .ui.cards>.card {
    width: 100%;
}

.ui.celled.grid>.column:not(.row), .ui.celled.grid>.row>.column {
    padding: 1em 0;
}

#area-form {
    padding: 0 1em;
}

#areas-list {
    padding: 0 1em;
}
#areas-list .ui.card,
#areas-list .ui.cards>.card {
    width: 100%;
    background-color: #333;
}

#areas-list .ui.card .meta,
#areas-list .ui.cards>.card .meta {
    color: #fff;
}
header {
    width: 70px;
}
.App-header {
    align-items: center;
    background-color: #fff;
    font-size: calc(10px + 2vmin);
    justify-content: center;
    height: 83px;
    width: 100%;
    border-right: 1px solid #dddbdb;
}
.App-header > a {
    background-color: #262626;
    box-shadow: -1px 2px 14px 0px #262626;
    height: 83px;
    margin: 0;
    padding: 10px 8px;
    text-decoration: none;
    width: 200px;
    display: block;
    float: left;
}

.App-header nav a {
    color: #dddbdb;
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    text-decoration: none;
}

.App-header nav {
    display: inline-block;
    padding-left: 3em;
    font-size: 24px;
}
.App-header img.ui.image {
    display: inline;
}
.App-header nav .ui.secondary.menu a.item {
    color: #262626;
    font-size: 20px;
    margin: 0;
    border-radius: 0;
}
.App-header nav .ui.secondary.menu a.item:hover {
    background-color: #262626;
    color: #fff;
}
.App-header nav .ui.secondary.menu {
    height: 83px
}
.marcopromo-app-container,
.marcopromo-app-container > div,
div#login,
div#login .login-form {
  height: 100%;
}
.logged-in.marcopromo-app-container > div {
  background-color: #D4D4D4;
}
.logged-out.marcopromo-app-container > div {
  background: url(/static/media/bg.5fa5b351.jpg);
  background-size: contain;
  overflow: hidden;
}
